<template>
  <div class="container">
    <div class="slideshow">
      <div class="menu">
        <ul>
          <li>
            <i class="material-icons"
               @click="openInfoPanel">info</i>
          </li> 
          <li>
            <i class="material-icons"
               @click="deleteButton" >delete</i>
          </li> 
        </ul> 
      </div> 
      <div class="fullscreen">
        <img :src="heroImage">
      </div>
    </div>
    <div class="infoPanel" :class="{ closeInfoPanel: !infoPanelOpen}">
      <div class="menu">
        <p>Infos</p>
        <i class="material-icons"
           @click="closeInfoPanel">close</i>
      </div>
      <h2>Details</h2>
      <div class="details">
        <dl>
          <div>
            <dt>
              <i class="material-icons">event</i>
            </dt>
            <dd>
              <p>{{date}}</p>
              <p>{{time}}</p> 
            </dd>
          </div>
          <div>
            <dt>
              <i class="material-icons">location_on</i>
            </dt>
            <dd>
              <div>
                <p>location</p>
              </div>  
              <p>{{latitude.toString().slice(0, 8)}}</p>
              <p>{{longitude.toString().slice(0, 8)}}</p> 
            </dd>
          </div>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { format, parse } from 'date-fns';
import { get } from 'lodash';

const {
  mapGetters: mapGettersObserve,
  mapActions: mapActionsObserve,
} = createNamespacedHelpers('observe');

export default {
  name: 'imageSlideshow',
  data () {
    const data = {
      selectedImg: 0,
      infoPanelOpen: false,
    };
    return data;
  },
  props: {
    observationId: {
      type: [Number, String],
      default () { return undefined; },
    },
  },
  components: {
    // 'observation-card': observationCard,
  },
  computed: {
    ...mapGettersObserve([
      'getObservationById',
    ]),
    obsId () {
      return Number(this.observationId);
    },
    activeDraft () {
      return this.getObservationById(this.obsId);
    },
    images () {
      const images = get(this.activeDraft, 'images');
      return images;
    },
    selectedImage () {
      const selectedImage = this.images[this.selectedImg];
      return selectedImage;
    },
    heroImage () {
      const imageData = get(this.selectedImage, 'data');
      if (imageData) {
        return URL.createObjectURL(imageData);
      }
      return undefined;
    },
    metadata () {
      return get(this.selectedImage, 'metadata');
    },
    date () {
      return format(parse(this.metadata.datetime), 'D MMM');
    },
    time () {
      return format(parse(this.metadata.datetime), 'ddd, HH:mm');
    },
    longitude () {
      return this.metadata.longitude;
    },
    latitude () {
      return this.metadata.latitude;
    },
  },
  methods: {
    ...mapActionsObserve([
      'removeImage',
    ]),
    deleteButton () {
      this.removeImage();
    },
    infoButton () {
      console.log('info clicked');
    },
    openInfoPanel () {
      this.infoPanelOpen = true;
    },
    closeInfoPanel () {
      this.infoPanelOpen = false;
    },
  },
};
</script>

<style lang="scss" scoped>
  .container{
    background-color: black;
  }

  .fullscreen {
    width: 100vw;
    margin-top: 2rem;
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

  .menu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 8px 20px 8px 32px;
    i {
      margin: .5rem;
    }
  }

  ul {
    color: white;
    display: flex;
    justify-content: flex-end;

    li {
      margin: 0 .5rem 0 .5rem;  
    }
  }

  .infoPanel {
    background: white;
    bottom: 0;
    left: auto;
    overflow-y: auto;
    position: fixed;
    right: 0;
    top: 3.1rem;
    width: 100vw;
    transition: all ease-in .3s;
    transform: translateX(0vw);
    .menu {
      justify-content: space-between;
      p {
        font-weight: 500;
        font-size: 20px;
      }
    }
    & > div, h2 {
      padding: 8px 20px 8px 32px;
    }
  }
  
  .closeInfoPanel {
    transform: translateX(100vw);
  }
  .details {
    display: flex;
    dl {
      display: flex;
      align-items: center;
      flex-direction: column;
      dt   {
        margin: 0 2rem 0 0;
      }
      div {
        display: flex;
        align-items: center;
      }
    }    
  }
</style>



